}

.all-user-events-modal .no-events {
  text-align: center;
  padding: 40px 20px;
  color: #6c757d;
  grid-column: 1 / -1;
}

/* Responsive design for the modal */
@media (max-width: 768px) {
  .all-user-events-modal .modal-content {
    margin: 10px;
    max-height: 95vh;
  }

  .all-user-events-modal .modal-header,
  .all-user-events-modal .modal-body {
    padding: 16px;
  }

  .all-user-events-modal .events-list {
    grid-template-columns: 1fr;
    gap: 12px;
  }

  .all-user-events-modal .event-item {
    flex-direction: column;
  }

  .all-user-events-modal .event-color-bar {
    width: 100%;
    height: 4px;
  }
}

@media (max-width: 576px) {
  .all-user-events-modal .modal-content {
