/* Loading State */
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.loading-spinner {
  text-align: center;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #043DFF;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 16px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-spinner p {
  margin: 0;
  color: #6c757d;
  font-weight: 500;
}

/* Error State */
.error-message {
  position: fixed;
  top: 20px;
  right: 20px;
  background: #f8d7da;
  color: #721c24;
  padding: 16px 20px;
  border-radius: 8px;
  border: 1px solid #f5c6cb;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  max-width: 400px;
}

.error-content {
  display: flex;
  align-items: center;
  gap: 12px;
}

.error-content svg {
  flex-shrink: 0;
}

.error-content p {
  margin: 0;
  flex: 1;
}

.retry-btn {
  padding: 6px 12px;
  border: 1px solid #721c24;
  background: transparent;
  color: #721c24;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.8rem;
  font-weight: 500;
  transition: all 0.2s ease;
}

.retry-btn:hover {
  background: #721c24;
  color: white;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .user-events-widget,
  .system-events-widget {
    padding: 20px;
  }
}

@media (max-width: 992px) {
  .calendar-content {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .user-events-widget,
  .system-events-widget {
    position: static;
    order: -1;
    max-width: 100%;
  }

  .calendar-mode-toggle {
    margin-bottom: 16px;
  }

  .mode-buttons {
    flex-direction: column;
    gap: 8px;
  }

  .mode-btn {
    min-width: auto;
  }
}

@media (max-width: 768px) {
  .calendar-container {
    padding: 16px;
  }

  .page-title {
    font-size: 2rem;
  }

  .calendar-navigation {
    flex-wrap: wrap;
    gap: 12px;
  }

  .current-month h2 {
    font-size: 1.25rem;
  }

  .calendar-day {
    min-height: 80px;
    padding: 4px;
  }

  .day-number {
    font-size: 0.8rem;
  }

  .event-dot {
    width: 6px;
    height: 6px;
  }

  .modal-content {
    margin: 10px;
    max-height: 90vh;
  }

  .modal-header,
  .modal-body {
    padding: 16px;
  }

  /* Make cards more compact on mobile */
  .user-events-widget,
  .system-events-widget {
    padding: 16px;
    margin: 0 -8px;
  }

  .user-event-item,
  .system-event-item {
    padding: 12px;
  }

  .event-info h4 {
    font-size: 0.9rem;
  }

  .event-date {
    font-size: 0.8rem;
  }

  .event-type-badge {
    font-size: 0.7rem;
    padding: 3px 6px;
  }
}

@media (max-width: 576px) {
  .header-actions {
    width: 100%;
    justify-content: center;
  }

  .view-mode-toggle {
    width: 100%;
    justify-content: center;
  }

  .view-btn {
    flex: 1;
  }

  .mode-buttons {
    width: 100%;
  }

  .mode-btn {
    flex: 1;
  }

  .calendar-days {
    gap: 0;
  }

  .calendar-day {
    min-height: 60px;
    padding: 2px;
  }

  .week-day {
    padding: 8px 4px;
    font-size: 0.8rem;
  }

  /* Extra compact cards for very small screens */
  .user-events-widget,
  .system-events-widget {
    padding: 12px;
    margin: 0 -8px;
    width: calc(100% + 16px);
  }

  .calendar-grid-container {
    width: calc(100% + 16px);
    margin: 0 -8px;
  }

  .user-event-item,
  .system-event-item {
    padding: 8px;
    margin-bottom: 8px;
  }

  .event-info h4 {
    font-size: 0.85rem;
    margin-bottom: 4px;
  }

  .event-date {
    font-size: 0.75rem;
    margin-bottom: 6px;
  }

  .event-type-badge {
    font-size: 0.65rem;
    padding: 2px 5px;
  }

  .widget-header h3 {
    font-size: 1.1rem;
  }

  .event-count {
    font-size: 0.7rem;
    padding: 3px 8px;
  }
}

/* Extra small screens - less than 430px */
@media (max-width: 430px) {
  .calendar-container {
    padding: 8px;
  }

  .page-title {
    font-size: 1.5rem;
  }

  .page-subtitle {
    font-size: 0.9rem;
  }

  /* Ultra compact cards for very small screens */
  .user-events-widget,
  .system-events-widget {
    padding: 8px;
    margin: 0 -4px;
    width: calc(100% + 8px);
  }

  .calendar-grid-container {
    width: calc(100% + 8px);
    margin: 0 -4px;
    padding: 12px;
  }

  .user-event-item,
  .system-event-item {
    padding: 6px;
    margin-bottom: 6px;
  }

  .event-info h4 {
    font-size: 0.8rem;
    margin-bottom: 3px;
    line-height: 1.2;
  }

  .event-date {
    font-size: 0.7rem;
    margin-bottom: 4px;
  }

  .event-type-badge {
    font-size: 0.6rem;
    padding: 2px 4px;
  }

  .widget-header h3 {
    font-size: 1rem;
  }

  .event-count {
    font-size: 0.65rem;
    padding: 2px 6px;
  }

  .widget-header {
    margin-bottom: 12px;
    padding-bottom: 8px;
  }

  /* Calendar navigation adjustments */
  .calendar-navigation {
    flex-direction: column;
    gap: 8px;
    align-items: center;
  }

  .nav-btn {
    width: 32px;
    height: 32px;
  }

  .current-month h2 {
    font-size: 1.1rem;
  }

  .today-btn {
    padding: 6px 12px;
    font-size: 0.8rem;
  }

  /* Calendar grid adjustments */
  .calendar-day {
    min-height: 50px;
    padding: 1px;
  }

  .day-number {
    font-size: 0.75rem;
  }

  .event-dot {
    width: 4px;
    height: 4px;
  }

  .week-day {
    padding: 6px 2px;
    font-size: 0.7rem;
  }

  /* Modal adjustments */
  .modal-content {
    margin: 5px;
    max-height: 95vh;
  }

  .modal-header,
  .modal-body {
    padding: 12px;
  }

  .modal-header h3 {
    font-size: 1.1rem;
  }

  .event-content h4 {
    font-size: 0.9rem;
  }

  .event-description {
    font-size: 0.8rem;
  }

  .event-meta {
    font-size: 0.7rem;
    gap: 8px;
  }
}

/* Extra extra small screens - less than 390px */
@media (max-width: 390px) {
  .calendar-container {
    padding: 4px;
  }

  .page-title {
    font-size: 1.3rem;
  }

  .page-subtitle {
    font-size: 0.8rem;
  }

  /* Maximum compact cards for extremely small screens */
  .user-events-widget,
  .system-events-widget {
    padding: 6px;
    margin: 0 -2px;
    width: calc(100% + 4px);
  }

  .calendar-grid-container {
    width: calc(100% + 4px);
    margin: 0 -2px;
    padding: 8px;
  }

  .user-event-item,
  .system-event-item {
    padding: 4px;
    margin-bottom: 4px;
  }

  .event-info h4 {
    font-size: 0.75rem;
    margin-bottom: 2px;
    line-height: 1.1;
  }

  .event-date {
    font-size: 0.65rem;
    margin-bottom: 3px;
  }

  .event-type-badge {
    font-size: 0.55rem;
    padding: 1px 3px;
  }

  .widget-header h3 {
    font-size: 0.9rem;
  }

  .event-count {
    font-size: 0.6rem;
    padding: 1px 4px;
  }

  .widget-header {
    margin-bottom: 8px;
    padding-bottom: 6px;
  }

  /* Calendar navigation adjustments */
  .calendar-navigation {
    gap: 6px;
  }

  .nav-btn {
    width: 28px;
    height: 28px;
  }

  .current-month h2 {
    font-size: 1rem;
  }

  .today-btn {
    padding: 4px 8px;
    font-size: 0.7rem;
  }

  /* Calendar grid adjustments */
  .calendar-day {
    min-height: 45px;
    padding: 0.5px;
  }

  .day-number {
    font-size: 0.7rem;
  }

  .event-dot {
    width: 3px;
    height: 3px;
  }

  .week-day {
    padding: 4px 1px;
    font-size: 0.65rem;
  }

  /* Modal adjustments */
  .modal-content {
    margin: 2px;
    max-height: 98vh;
  }

  .modal-header,
  .modal-body {
    padding: 8px;
  }

  .modal-header h3 {
    font-size: 1rem;
  }

  .event-content h4 {
    font-size: 0.85rem;
  }

  .event-description {
    font-size: 0.75rem;
  }

  .event-meta {
    font-size: 0.65rem;
    gap: 6px;
  }
}

/* RTL Support */
.calendar-container.rtl .search-input {
  padding: 12px 48px 12px 16px;
}

.calendar-container.rtl .search-icon {
  left: auto;
  right: 16px;
}


.calendar-container.rtl .event-color-bar {
  order: 1;
}

.calendar-container.rtl .event-content {
  order: 0;
}
